<template>
  <div id="map" class='rel'>
    <FeatureMap :drawFeature="drawFeature"/>
    <mapLoader v-if="showLoader"/>
  </div>
</template>

<script>
import axios from 'axios'
import * as d3 from 'd3'
import FeatureMap from '../Features/FeatureMap.vue'
import * as panel3d from '../StructureViewer/helpers/joleculeAPI'
import { mapGetters } from 'vuex'
import mapLoader from './mapLoader.vue'
import store from '../../store/index'
import * as View from '../InfoAbout/helpers/ViewUtils'
import { findPDBMember } from '../../utils/loadData'

// var w = window.outerWidth
var h = 50
var transitionVar = 200
var greyScale = [61, 28]
var svgElem
export default {
  name: 'CoverageMap',
  components: {
    FeatureMap,
    mapLoader
  },
  data () {
    return {
      seqLength: 0,
      scale: null,
      coverageMapsrw: 0,
      residueColor: null,
      densityScale: null,
      darkRegions: [],
      myArray: [1],
      coverageMapData: null,
      drawFeature: false,
      showLoader: true,
      structure: {
        accession: null
      },
      docFrag: null
    }
  },
  computed: {
    getLoadedFeature () {
      return store.state.loadedFeature
    },
    secondaryStructure () {
      return store.state.secondaryStructure
    },
    sequence () {
      return store.state.currentSequence
    },
    loader () {
      return store.state.Loader
    },
    ...mapGetters([
      'getGene',
      'getStructure',
      'getOneAaCodes',
      'getThreeAaCodes',
      'getLoadedFeature',
      'getSequences',
      'getCoveragemapOverlay',
      'getPdbIndex'
    ])
  },
  watch: {
    getCoveragemapOverlay (value) {
      this.showLoader = value
    },
    getLoadedFeature (newCount) {
      var _this = this
      var PDBIndex = findPDBMember(_this.getStructure.pdb, _this.getStructure.chain)
      var currentMember = store.state.Matches.clusters[PDBIndex.cluster].members[PDBIndex.member]
      if (newCount.set === null) {
        _this.drawFeature = false
      }
      if (!document.querySelector('#map > #coverageMap') && _this.getStructure.accession) {
        _this.updateUI()
      } else {
        _this.setStructFeatmap(currentMember)
        _this.drawFeature = true
      }
    },
    secondaryStructure () {
      // Update structure map when new structure selected
      var _this = this
      if (document.querySelector('#map > #coverageMap')) {
        var PDBIndex = findPDBMember(_this.getStructure.pdb, _this.getStructure.chain)
        var currentMember = store.state.Matches.clusters[PDBIndex.cluster].members[PDBIndex.member]
        _this.selectStructmap(currentMember)
      } else {
        if (_this.getPdbIndex.member >= 0 && _this.getStructure.accession) {
          _this.updateUI()
        }
      }
    },
    sequence (newVal, oldVal) {
      // Render new coverage map if new chain selected
      var _this = this
      var checkSeq = false
      if (oldVal) {
        if (oldVal.primary_accession !== newVal.primary_accession) {
          checkSeq = true
        } else if (oldVal.sequence === newVal.sequence) {
          store.commit('setCoveragemapOverlay', false)
        }
      } else {
        checkSeq = true
      }
      if (checkSeq) {
        if (document.querySelector('#map > #coverageMap')) {
          document.querySelector('#map > #coverageMap').remove()
        }
        _this.init(newVal.primary_accession)
        var loadRequest = {
          selector: [newVal.primary_accession],
          selectPDB: this.$store.state.structure.pdb, // optional fields
          selectChain: this.$store.state.structure.chain // optional fields
        }
        var url = `${window.BACKEND}/get_3D_alignment`
        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          const newData = response.data
          store.commit('setSecondaryStructure', newData.secondary_structure)
        })
      }
    },
    loader: function (newVal) {
      // Render coverage map after structure finishes loading
      var _this = this
      if (!newVal) {
        const joleculeWatcher = new View.JoleculeWatcher(panel3d.embededJolecule)
        joleculeWatcher.addEventListener('viewcommit', () => {
          if (!document.querySelector('#map > #coverageMap') && _this.getStructure.accession) {
            _this.updateUI()
          }
        })
      }
    }
  },
  mounted () {
    // Fetch coverage map data
    var _this = this
    if (this.getStructure.accession) {
      _this.init(_this.getStructure.accession)
    }
  },
  methods: {
    updateUI () {
      // Render coverage map
      var _this = this
      // Render coverage map for the first time
      if (!document.querySelector('#map > #coverageMap')) {
        _this.drawMap(_this.coverageMapData)
        document.querySelector('#map').appendChild(_this.docFrag)
        // If feature applied, draw feature map
        var PDBIndex = findPDBMember(_this.getStructure.pdb, _this.getStructure.chain)
        var currentMember = store.state.Matches.clusters[PDBIndex.cluster].members[PDBIndex.member]
        if (_this.getLoadedFeature.set !== null) {
          _this.setStructFeatmap(currentMember)
          _this.drawFeature = true
        } else {
          _this.selectStructmap(currentMember)
        }
        store.commit('setCoverageMap', true)
        store.commit('setCoveragemapOverlay', false)
      }
    },
    init (accession) {
      // Get the data
      var _this = this
      store.commit('setCoveragemapOverlay', true)
      var url = `${window.BACKEND}/getCoverageMap`
      var loadRequest = {
        selector: [accession]
      }
      axios({
        method: 'get',
        url: url,
        params: loadRequest
      }).then(function (response) {
        console.log(response.data)
        _this.coverageMapData = response.data
      })
    },
    drawMap (data) {
      // Draw outline of the map without any structure map selected
      var _this = this
      svgElem = _this.renderSVG()
      svgElem.append('rect')
        .attr('id', 'selection')
      this.drawLine(svgElem)
      this.setScales(data)
      this.drawDarkRegions(data)
      data.secondaryStructures.forEach(function (d) {
        // convert strings to numbers
        d.start = +d.start
        d.end = +d.end
        _this.drawResidues(d, data)
      })
    },
    setScales (data) {
      var _this = this
      this.seqLength = data.seqLength
      this.scale = d3.scale.linear().domain([1, this.seqLength]).range([1, window.innerWidth]) // Scale seq length by screen width
      this.coverageMapsrw = (this.scale(data.seqLength)) / (data.seqLength) // Single residue width
      var maxStruct = Math.max(...data.densityMap)
      if (maxStruct <= transitionVar) { // Use absolute scale for density map when total number of struct < 200
        _this.densityScale = d3.scale.linear().domain([Math.log(1), Math.log(transitionVar)]).range(greyScale)
      } else { // Use relative scale for density map when total number of struct > 200
        _this.densityScale = d3.scale.linear().domain([Math.log(1), Math.log(Math.max(...data.densityMap))]).range(greyScale)
      }
    },
    drawLine (svg) {
      svg
        .append('line')
        .style('stroke', '#434343')
        .attr('x1', 1)
        .attr('y1', 26)
        .attr('x2', window.innerWidth)
        .attr('y2', 26)
    },
    renderSVG () {
      this.docFrag = document.createDocumentFragment()
      var svg = d3
        // .select('#map')
        .select(this.docFrag)
        .append('svg')
        .attr('id', 'coverageMap')
        .attr('height', h)
        .attr('width', '100%')
        .attr('viewBox', '0, 0, ' + window.innerWidth + ',' + h)
        .attr('preserveAspectRatio', 'none')
        // .attr('height', 40)

      var rect = svg.append('g')
      return rect
    },
    drawResidues (el, data) {
      var svg = svgElem
      // var thickness = 0
      var ptext = ''
      var ptitle = ''
      var _this = this
      if (el.Type === 'SHEET') {
        var height = 18
        // var color = '#f6c719'
        var translate = 17
        var className = 'sheet'
      } else if (el.Type === 'COIL') {
        height = 14
        // color = '#6f9e52'
        translate = 19
        className = 'coil'
      } else {
        height = 19
        // color = '#6491b6'
        translate = 16
        className = 'helix'
      }
      for (var n = el.start; n <= el.end; n++) {
        var color = 'hsl(0, 0%,' + _this.densityScale(Math.log(data.densityMap[n - 1])) + '%)'
        svg
          .append('g')
          .attr('id', 'residue_' + n + '_' + el.Type + '_' + data.densityMap[n - 1])
          .append('rect')
          .attr('transform', 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + translate + ')')
          .attr('width', function () {
            if (_this.coverageMapsrw < 0.5) {
              return _this.coverageMapsrw + 0.25
            } else {
              return _this.coverageMapsrw
            }
          })
          .attr('height', height)
          .attr('fill', color)
          .attr('class', className)
          .attr('stroke-width', 1)
          .on('mouseover', function (d) {
            _this.residueColor = d3.select(this).style('fill') // Store original residue color
            d3.select(this)
              .attr('stroke', 'white')
              .attr('stroke-width', 1)
            var resNum = this.parentElement.id.split('_')[1]
            store.commit('setRes', resNum)
            var sequenceAa = _this.getSequences[0].sequence[resNum - 1]
            var idx = _this.getOneAaCodes.indexOf(sequenceAa)
            var longAa = _this.getThreeAaCodes[idx]
            // @TODO Fetch amino acid codes in store
            ptitle = '<span style="font-weight: 700;">' + _this.getGene + '</span> sequence: ' + longAa + '–' + resNum
            if (this.parentElement.id.includes('active')) {
              // @TODO Create separate method to fetch structure amino acid code
              var jAlignment = panel3d.joleculeAlignment
              var jController = panel3d.embededJolecule.controller
              var pdbResNum = jAlignment.mapSeqResToPdbResOfChain(0, resNum, _this.getStructure.chain)
              var iRes = jController.soup.findResidueIndices(0, pdbResNum[1], pdbResNum[2])
              var structAa = jController.soup.getResidueProxy().load(iRes).resType
              structAa = structAa.substr(0, 1) + structAa.substr(1).toLowerCase()
              ptext = _this.getStructure.pdb + '-' + _this.getStructure.chain + ' structure: ' + structAa + '-' + pdbResNum[2] + '</br> Structures with residue ' + this.parentElement.id.split('_')[1] + ': '
            } else {
              ptext = 'Structures with residue ' + this.parentElement.id.split('_')[1] + ': '
            }
            store.commit('setPopupTitle', ptitle)
            store.commit('setPopupText', ptext)
            store.commit('setTotalPDBres', this.parentElement.id.split('_')[3])
            store.commit('setPopupCoordinates', this.getBoundingClientRect())
            store.commit('setPopupType', 'coveragemap')
          })
          .on('mouseout', function () {
            setTimeout(function () {
              if (!document.getElementById('loadedPin').classList.contains('loadedPin_active')) {
                store.commit('setPopupCoordinates', 0)
                store.commit('setPopupType', null)
              }
            }, 5000)
            d3.select(this)
              .attr('stroke', _this.residueColor)
              .attr('stroke-width', 0)
          })
      }
    },
    // This function resets the coverage map residue properties
    // And highlights the selected region in the coverage map
    selectStructmap (selectedClus) {
      var _this = this
      d3.selectAll('[id^="residue_"')[0].map(function (d, i) { // Reset coverage map residue properties
        var density = d.id.split('_')[3]
        if (d.id.includes('active')) {
          d.id = d.id.split('_active')[0]
        }
        d3.select(d)
          .select('rect')
          .attr('fill', 'hsl(0, 0%,' + _this.densityScale(Math.log(density)) + '%)')
      })
      var secondaryStruct = store.getters.getSecondaryStructure[0]
      var seqStart = selectedClus.seq_start[0]
      var seqEnd = selectedClus.seq_end[selectedClus.seq_start.length - 1]
      var width = (seqEnd - seqStart) * _this.coverageMapsrw + _this.coverageMapsrw

      if (document.querySelector('#map > #coverageMap > rect')) {
        document.querySelector('#map > #coverageMap > rect').remove()
      }
      d3.select('#map > #coverageMap > g').select('rect#selection')
        .attr('width', width)
        .attr('height', 30)
        .attr('transform', 'translate(' + (seqStart * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 11 + ')')
        .attr('fill', 'white')
        .attr('stroke', '#434343')
        .attr('stroke-width', 1)
        .attr('rx', 6)
        .attr('ry', 6)

      secondaryStruct.map(function (index) {
        if (index.end) {
          var start = index.start
          var end = index.end
          var type = index.type
        } else {
          start = index.split('\n')[0].split(':')[1].split('-')[0]
          end = index.split('\n')[0].split(':')[1].split('-')[1]
          type = index.split('\n')[0].split(':')[0]
        }
        for (var j = start; j <= end; j++) {
          var elemId = '[id^="residue_' + j + '_"]'
          // var type = document.querySelector(elemId).id.split('_')[2]
          d3.select(elemId + '> rect').attr({
            fill: _this.getStructureColor(type)
          })
          // Indicate which residues are active
          if (!document.querySelector(elemId)) { // TO DO: Fix secondary structure calculation on server-side
            var el = {}
            el.Type = 'COIL'
            el.start = j
            el.end = j
            _this.drawResidues(el, _this.coverageMapData)
            document.querySelector(elemId).id = document.querySelector(elemId).id + '_active'
          } else {
            document.querySelector(elemId).id = document.querySelector(elemId).id + '_active'
          }
        }
      })
    },
    setStructFeatmap (selectedClus) {
      var _this = this
      // Select correct region in the coverage map
      d3.selectAll('[id^="residue_"')[0].map(function (d, i) {
        var density = d.id.split('_')[3]
        d3.select(d)
          .select('rect')
          .attr('fill', 'hsl(0, 0%,' + _this.densityScale(Math.log(density)) + '%)')
      })
      var secondaryStruct = store.getters.getSecondaryStructure[0]
      var seqStart = selectedClus.seq_start[0]
      var seqEnd = selectedClus.seq_end[selectedClus.seq_start.length - 1]
      var width = (seqEnd - seqStart) * _this.coverageMapsrw + _this.coverageMapsrw

      if (document.querySelector('#map > #coverageMap > rect')) {
        document.querySelector('#map > #coverageMap > rect').remove()
      }
      d3.select('#map > #coverageMap > g > #selection')
        .attr('width', width)
        .attr('height', 30)
        .attr('transform', 'translate(' + (seqStart * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 11 + ')')
        .attr('fill', 'white')
        .attr('stroke', '#434343')
        .attr('stroke-width', 1)
        .attr('rx', 6)
        .attr('ry', 6)

      secondaryStruct.map(function (index) {
        if (index.end) {
          var start = index.start
          var end = index.end
        } else {
          start = index.split('\n')[0].split(':')[1].split('-')[0]
          end = index.split('\n')[0].split(':')[1].split('-')[1]
        }
        for (var j = start; j <= end; j++) {
          var elemId = '[id^="residue_' + j + '_"]'
          // var type = document.querySelector(elemId).id.split('_')[2]
          d3.select(elemId + '> rect').attr({
            fill: '#cccccc'
          })
        }
      })
    },
    getStructureColor (type) {
      if (type === 'SHEET') {
        return '#f6c719'
      } else if (type === 'COIL') {
        return '#6f9e52'
      } else {
        return '#6491b6'
      }
    },
    drawDarkRegions (data) {
      var arr = []
      var _this = this
      var svg = svgElem
      data.densityMap.forEach(function (item, index) {
        if (item === 0) {
          var n = index + 1
          svg
            .append('g')
            .attr('id', 'residue_' + n + '_Dark_0')
            .append('rect')
            .attr('transform', 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 14 + ')')
            .attr('width', function () {
              if (_this.coverageMapsrw < 0.5) {
                return _this.coverageMapsrw + 0.25
              } else {
                return _this.coverageMapsrw
              }
            })
            .attr('height', 30)
            .attr('fill', '#434343')
            .attr('opacity', 0)
            .attr('stroke-width', 1)
            .on('mouseover', function (d) {
              _this.residueColor = d3.select(this).style('fill') // Store original residue color
              d3.select(this)
                .attr('stroke', 'white')
                .attr('stroke-width', 1)
              store.commit('setPopupTitle', 'Residue ' + this.parentElement.id.split('_')[1] + ' : Dark region')
              store.commit('setPopupText', 'Structures with residue ' + this.parentElement.id.split('_')[1] + ': ')
              store.commit('setTotalPDBres', this.parentElement.id.split('_')[3])
              store.commit('setPopupCoordinates', this.getBoundingClientRect())
              store.commit('setPopupType', 'coveragemap')
            })
            .on('mouseout', function () {
              setTimeout(function () {
                if (!document.getElementById('loadedPin').classList.contains('loadedPin_active')) {
                  store.commit('setPopupCoordinates', 0)
                  store.commit('setPopupType', null)
                }
              }, 5000)
              d3.select(this)
                .attr('stroke', _this.residueColor)
                .attr('stroke-width', 0)
            })
        }
      })
      return arr
    }
  }
}
</script>

