    right: 1rem;
    background-color: var(--primary-tab);
    opacity: 1;
    height: auto;
    order: 3;
    display: flex;
    flex-direction: column;
    /* width: 100%; */
    transition: All 0.8s ease;
    }
  }

  #Menu div.lnk {
