<template>
    <div id='featureMap'></div>
</template>

<script>
import store from '../../store/index'
import * as d3 from 'd3'
import { mapGetters } from 'vuex'

export default {
  name: 'FeatureMap',
  props: ['drawFeature'],
  data () {
    return {
      seqLength: 0,
      scale: null,
      coverageMapsrw: 0,
      residueColor: null
    }
  },
  computed: {
    getLoadedFeature () {
      return store.state.loadedFeature
    },
    ...mapGetters([
      'getVariantResidues',
      'getLoadedFeature',
      'getVariantIdx',
      'getVariant',
      'getMatches'
    ])
  },
  watch: { // Watch if new feature is loaded
    getLoadedFeature () {
      var _this = this
      if (_this.getLoadedFeature.set === null) {
        // history.pushState(null, '', window.location.href.split('?')[0] + window.location.hash)
        document.querySelectorAll('[id^="feature_residue_').forEach(function (el) { el.remove() })
      } else {
        if (document.querySelector('[id^="feature_residue_')) { // check if a feature is already selected. If so, remove and redraw
          document.querySelectorAll('[id^="feature_residue_').forEach(function (el) { el.remove() })
          _this.drawMap(_this.getLoadedFeature)
        } else {
          _this.drawMap(_this.getLoadedFeature)
        }
      }
    }
  },
  methods: {
    drawMap (data) {
      var _this = this
      this.setScales()
      data.set.Tracks[0].forEach(function (d, i) {
        // convert strings to numbers
        d.start = +d.start
        d.end = +d.end
        _this.drawFeatureResidues(d)
      })
      if (!this.$store.getters.getIsDefaultFeature && this.getVariantResidues) { // if feature is a variant, open popup
        _this.loadPopup(data)
        console.log('The data here is ' + _this.getVariantIdx + ' ' + _this.getVariant)
        console.log(data)
      }
    },
    loadPopup (data) {
      var _this = this

      var allKeys = Object.keys(_this.getVariantResidues).sort(function (a, b) {
        return _this.getVariantResidues[a].order - _this.getVariantResidues[b].order
      })
      var res = allKeys[0]
      var id = '[id^="feature_residue_' + res + '"]'
      var elem = document.querySelector(id)
      data.set.Tracks[0].forEach(function (el) {
        if (_this.getVariantResidues[el.start]) {
          var popupText = _this.getVariantResidues[el.start]
          store.commit('setPopupTitle', el.label)
          store.commit('setPopupText', popupText)
          store.commit('setVariantIdx', 0)
          store.commit('setIsDefaultFeature', false)
          store.commit('setVariant', el.start)
          if (elem != null) {
            store.commit('setPopupCoordinates', elem.getBoundingClientRect())
          }
        }
      })
    },
    setScales () {
      this.seqLength = this.getMatches.sequences[0].length
      this.scale = d3.scale.linear().domain([1, this.seqLength]).range([1, window.innerWidth])
      this.coverageMapsrw = (this.scale(this.seqLength)) / (this.seqLength)
    },
    drawFeatureResidues (el) {
      var _this = this
      var popupText = ''
      var translate = ''
      var fResidueHeight = ''
      for (var n = el.start; n <= el.end; n++) {
        var elem = '[id^="residue_' + n + '"]'
        var tmp1 = document.querySelector(elem)
        var tmp2 = document.querySelector(elem + '> rect')
        if (tmp1 === 'undefined' || tmp2 === 'undefined' || tmp1 == null || tmp2 == null) {
          continue
        } else {
          if (tmp1.id.includes('Dark')) {
            translate = 15
            fResidueHeight = 20
          } else {
            translate = 25
            fResidueHeight = document.querySelector(elem + '> rect').getBoundingClientRect().height
          }
          var covMap = d3.select('#map > #coverageMap > g')
          covMap
            .append('g')
            .attr('id', 'feature_residue_' + n + '_' + document.querySelector(elem).id.split('_')[3])
            .append('rect')
            .attr('width', _this.coverageMapsrw)
            .attr('height', fResidueHeight)
            .attr('transform', function () {
              var elemHeight = document.querySelector(elem + '> rect').getBoundingClientRect().height
              if (elemHeight === 18) {
                return 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 17 + ')'
              } else if (elemHeight === 14) {
                return 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 19 + ')'
              } else if (elemHeight === 19) {
                return 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + 16 + ')'
              } else {
                return 'translate(' + (n * _this.coverageMapsrw - _this.coverageMapsrw) + ',' + translate + ')'
              }
            })
            .attr('fill', el.color)
            .on('mouseover', function (d) {
              _this.residueColor = d3.select(this).style('fill') // Store original residue color
              d3.select(this)
                .attr('stroke', 'white')
                .attr('stroke-width', 1)
              if (tmp1.id.includes('Dark')) {
                store.commit('setPopupTitle', el.label + ': Dark region')
              } else {
                store.commit('setPopupTitle', el.label)
              }
              if (el.desc !== el.label) {
                if (_this.getVariantResidues[el.start]) {
                  popupText = _this.getVariantResidues[el.start]
                  //   popupText += 'Structures with residue ' + this.parentElement.id.split('_')[1] + ': '
                  store.commit('setPopupText', popupText)
                  store.commit('setVariantIdx', 0)
                  store.commit('setVariant', el.start)
                } else if (el.desc.length > 0 && el.desc !== 'undefined') {
                  popupText = el.desc + ' </br> '
                  popupText += 'Structures with residue ' + this.parentElement.id.split('_')[1] + ': '
                  store.commit('setPopupText', popupText)
                  store.commit('setVariant', -1)
                  store.commit('setIsDefaultFeature', false)
                } else {
                  store.commit('setPopupText', 'Structures with residue ' + this.parentElement.id.split('_')[2] + ': ')
                }
              } else {
                store.commit('setPopupText', 'Structures with residue ' + this.parentElement.id.split('_')[2] + ': ')
                store.commit('setVariant', -1)
              }
              store.commit('setIsDefaultFeature', false)
              store.commit('setTotalPDBres', this.parentElement.id.split('_')[3])
              store.commit('setPopupCoordinates', this.getBoundingClientRect())
              store.commit('setPopupType', 'coveragemap')
            })
            .on('mouseout', function () {
              d3.select(this)
                .attr('stroke', _this.residueColor)
                .attr('stroke-width', 0)
            })
        }
      }
    }
  }
}
</script>

<style>
#featureMap {
    position: relative;
    bottom: 47px ;
}
</style>
