<template>
    <div>
        <div class="scrim level6" @click="hideTree"></div>
        <div id='arc' class="level7"></div>
    </div>
</template>

<script>
import * as d3 from 'd3'
import * as d3Shape from 'd3-shape'
import { mapGetters } from 'vuex'
import store from '../../store/index'
import * as showMatchingStructures from '../MatchingStructures/helpers/show_matching_structures'
import * as loadData from '../../utils/loadData'

var limit = 10
const w = 450
const h = 450
const radius = 14
var featureRegex = new RegExp(/(p\.)?[A-Za-z]+[0-9]+[A-Za-z\*\_\?\[\]\(\)\%\=\.]+/) //eslint-disable-line

export default {
  name: 'TreeView',
  props: ['treeReady', 'count', 'coordinates', 'parentElem'],
  data () {
    return {
      pdbs: [],
      up: require('../../assets/img/up.png'),
      down: require('../../assets/img/down.png')
    }
  },
  computed: {
    ...mapGetters([
      'getGene',
      'getOrganism',
      'getMatches'
    ])
  },
  watch: {
    treeReady: function (newVal, oldVal) { // watch it
      var _this = this
      if (_this.coordinates.clientX + w + 5 >= window.innerWidth && window.innerWidth > 500) {
        document.querySelector(_this.parentElem + ' #arc').style.left = (window.innerWidth - w - 5) + 'px'
      } else if (_this.coordinates.clientX + w + 5 >= window.innerWidth) {
        document.querySelector(_this.parentElem + ' #arc').style.left = '50px'
      } else {
        document.querySelector(_this.parentElem + ' #arc').style.left = _this.coordinates.clientX + 'px'
      }
      document.querySelector(_this.parentElem + ' #arc').style.top = (_this.coordinates.clientY - 400) + 'px'
      if (this.$parent.showLoader === true) {
        this.$parent.showLoader = false
      } else {
        this.$parent.showTree = false
      }
      console.log(_this.count)
      console.log('Prop changed: ', newVal, ' | was: ', oldVal)
      var pdbs = newVal
      var parent = pdbs[0]
      d3.selectAll(_this.parentElem + ' #arc > *').remove()
      if (pdbs.length > limit) {
        var chunks = _this.chunk(pdbs, limit)
        _this.generateArc(chunks, parent, 0)
      } else {
        _this.generateArc(pdbs, parent)
      }
    }
  },
  methods: {
    hideTree () {
      store.commit('setTreeData', null)
      this.$parent.showTree = false
    },
    nodeClick (d) {
      var _this = this
      var parent = d[2].children[0]
      var index = 0
      parent.name = d[2].label
      parent.length = d[2].children.length
      if (d[2].children.length > limit) {
        var chunks = _this.chunk(d[2].children, limit)
        _this.generateArc(chunks, parent, index)
        // chunks.forEach(function (chunk) {
        //   index = index + 1
        // })
      } else {
        index = index + 1
        this.generateArc(d[2].children, parent)
      }
    },
    chunk (items, size) {
      const chunks = []
      items = [].concat(...items)

      while (items.length) {
        chunks.push(
          items.splice(0, size)
        )
      }

      return chunks
    },
    reversePdbArray (array) {
      var outputArray = []
      array.forEach(function (arr) {
        outputArray.unshift(arr)
      })
      return outputArray
    },
    generateArc (pdblist, parentPDB, index) {
      var _this = this
      var gap = 60
      var node, pdbs, nodes, elems, pdbArray
      if (index !== undefined) {
        pdbArray = pdblist
        pdbs = pdbArray[index]
      } else {
        pdbArray = _this.reversePdbArray(pdblist)
        pdbs = pdbArray
      }

      if (Object.prototype.hasOwnProperty.call(pdbs[0], 'children')) {
        if (index !== undefined && document.querySelector('svg#tree') !== null) {
          document.querySelector('svg#tree').remove()
        }
        var svg = d3
          .select(_this.parentElem + ' #arc')
          .append('svg')
          .attr('id', 'tree')
          .attr('width', w)
          .attr('height', h)

        elems = _this.createLinks(svg, 'parent', pdbArray, parentPDB, index)
      } else {
        document.querySelectorAll('outerElem').forEach(el => el.remove())
        document.querySelectorAll('[id^=children_]').forEach(e => e.parentNode.removeChild(e))
        svg = d3
          .select(_this.parentElem + ' #arc')
          .append('svg')
          .attr('id', 'children_' + pdbs[0].pdb_id + '_' + index)
          .attr('width', w)
          .attr('height', h)

        elems = _this.createLinks(svg, 'child', pdbArray, parentPDB, index)
      }

      nodes = elems.nodes
      svg = elems.svg

      // Append the nodes to the svg element
      if (Object.prototype.hasOwnProperty.call(pdbs[0], 'children')) {
        nodes.shift()
        node = d3.select('svg#tree')
          .selectAll('g#gParent')
          .data(nodes)
          .enter()
      } else {
        parentPDB.dx = (nodes[1][0] - gap)
        parentPDB.dy = (nodes[1][1] - radius / 2)
        nodes.push([nodes[1][0] - gap, nodes[1][1], parentPDB, 'parent'])
        document.querySelector('#tree').classList.add('hide')
        nodes.shift()
        node = d3
          .select('svg#children_' + pdbs[0].pdb_id + '_' + index)
          .selectAll('g')
          .data(nodes)
          .enter()
      }

      node = node
        .append('g')
        .attr('class', function (d, i) {
          if (d.length > 1) {
            if (Object.prototype.hasOwnProperty.call(d[2], 'children')) {
              return 'outerElem'
            } else {
              return 'innerElem'
            }
          }
        })
        .attr('id', function (d, i) {
          //             if (d[2].member === 0 && d[2].depth === 0) { d[2].id = 0 }
          if (d.length > 1) {
            if (Object.prototype.hasOwnProperty.call(d[2], 'children')) {
              return 'out_' + d[2].children[0].pdb_id
            } else if (d[3] !== undefined && d[3].includes('parent')) {
              return 'parentNode'
            } else {
              return 'in_' + d[2].pdb_id
            }
          }
        })
        .attr('width', radius)
        .attr('height', radius)
        .on('click', function (d, i) {
          if (i >= 0) {
            if (Object.prototype.hasOwnProperty.call(d[2], 'children')) {
              // Initiate nodeClick if parent node
              _this.nodeClick(d)
            } else {
              if (d[3] === undefined) { // Clicked on child node
                var urlParams = window.location.search
                history.pushState('', document.title, '/' + _this.getOrganism + '/' + _this.getGene + '/' + d[2].pdb_id + '/' + d[2].pdb_chain + urlParams)
                _this.getMatches.clusters.forEach(function (cluster, i) {
                  if (cluster.members.length > 1) {
                    cluster.members.forEach(function (member) {
                      if (member.pdb_id === d[2].pdb_id && member.pdb_chain[0] === d[2].pdb_chain) {
                        loadData.displayMember(member)
                        showMatchingStructures.selectCluster(cluster.pdb_id, i)
                        return null
                      }
                    })
                  } else {
                    if (cluster.members[0].pdb_id === d[2].pdb_id && cluster.members[0].pdb_chain[0] === d[2].pdb_chain) {
                      cluster.E_value = cluster.members[0].E_value
                      loadData.displayMember(cluster)
                      showMatchingStructures.selectCluster(cluster.pdb_id, i)
                      return null
                    }
                  }
                })
                if (featureRegex.test(urlParams)) {
                  _this.waitForElement()
                }
              } else { // Clicked on parent node
                document.querySelector('#tree').classList.remove('hide')
                document.querySelectorAll('[id^=children_]').forEach(e => e.parentNode.removeChild(e))
              }
            }
          }
        })

      if (Object.prototype.hasOwnProperty.call(pdbs[0], 'children')) {
        node.append('rect')
          .attr('transform', function (d) {
            return 'translate(' + d[0] + ',' + (d[1] - radius / 2) + ')'
          })
          .attr('class', 'label')
          .attr('width', function (d, i) {
            return d[2].children ? (d[2].children.length > 9 ? 32 : 26) : 0
          }
          )
          .attr('height', function (d, i) {
            return d[2].children ? 16 : 0
          })
          .attr('rx', 6)
          .attr('fill', '#a3a3a3')
          .attr('stroke', 'none')
      } else {
        var pNode = d3.select('g#parentNode').selectAll('g').data([parentPDB])
          .enter()

        pNode.append('text')
          .attr('x', function (d) { return d.dx - 40 - 20 })
          .attr('dy', function (d) { return d.dy + 40 })
          .attr('class', 'nbr_label')
          .text(function (d) {
            if (d.name.length > 15) { return d.name.substr(0, 15) + '...' } else { return d.name }
          })
          .attr('text-anchor', 'start')
          .style('fill-opacity', 1)
          .style('fill', 'white')
          .style('font-weight', 500)

        pNode.append('rect')
          .attr('transform', function (d) {
            return 'translate(' + d.dx + ',' + d.dy + ')'
          })
          .attr('class', 'label')
          .attr('width', function (d, i) {
            return d.length ? (d.length > 9 ? 32 : 26) : 0
          })
          .attr('height', 16)
          .attr('rx', 6)
          .attr('fill', '#a3a3a3')
          .attr('stroke', 'none')

        pNode.append('text')
          .attr('x', function (d) { return d.dx + radius + 2 })
          .attr('dy', function (d) { return d.dy + radius - 1 })
          .attr('class', 'nbr_label')
          .text(function (d) {
            return d.length
          })
          .attr('text-anchor', 'start')
          .style('fill-opacity', 1)
          .style('fill', 'white')
          .style('font-weight', 500)
      }

      node
        .append('circle')
        .attr('cx', function (d) { return d[0] })
        .attr('cy', function (d) { return d[1] })
        .attr('r', (radius))
        .style('fill', 'white')
        .style('stroke', '#999999')
        .style('stroke-width', '1')

      node.append('clipPath')
        .attr('id', 'clip_' + function (d, i) { return i })
        .append('circle')
        .attr('cx', function (d) { return d[0] })
        .attr('cy', function (d) { return d[1] })
        .attr('r', (radius))
        .attr('clip-rule', 'evenodd')

      node.append('image')
        .attr('width', 2 * (radius - 1) + 'px')
        .attr('height', 2 * (radius - 1) + 'px')
        .attr('x', function (d) { return d[0] - 13 })
        .attr('y', function (d) { return d[1] - 13 })
        .attr('clip-path', function (d, i) {
          var pathNm = ' '
          if (d[2].pdb_id) { pathNm = 'url(#clip_' + d[2].pdb_id + ')' }
          if (d[2].children && d[2].children[0].pdb_id) { pathNm = 'url(#clip_' + d[2].children[0].pdb_id + ')' }
          return pathNm
        })
        .attr('xlink:href', function (d, i) {
          var imgNm = ' '
          if (d[2].pdb_id && typeof d[2].children === 'undefined') { imgNm = d[2].pdb_id.toUpperCase() }
          if (d[2].children && d[2].children[0].pdb_id) { imgNm = d[2].children[0].pdb_id.toUpperCase() }
          return (imgNm === ' ') ? ' ' : 'https://www.ebi.ac.uk/pdbe/static/entry/' + imgNm.toLowerCase() + '_assembly_1_chain_front_image-200x200.png'
          // return (imgNm === " ") ? " " : "https://www.rcsb.org/pdb/images/"+imgNm+"_bio_r_65.jpg";
        })

      node.append('text')
        .attr('x', function (d) { return d[0] + radius + 2 })
        .attr('dy', function (d) { return d[1] + radius / 2 - 1 })
        .attr('class', 'nbr_label')
        .text(function (d, i) {
          if (d[2].label && d[2].label !== 'undefined') {
            var nodeLabel = d[2].label
            if (d[2].label.length > 30) { nodeLabel = d[2].label.substr(0, 15) + ' ... ' + d[2].label.substr(d[2].label.length - 15) }
            return d[2].children ? d[2].children.length + '      ' + nodeLabel : ' '
          } else {
            if (d[3] === undefined) {
              return d[2].pdb_id + '-' + d[2].pdb_chain
            }
          }
        })
        .attr('title', function (d, i) {
          return (d[2].label_long) ? d[2].label_long : d[2].label
        })
        .attr('text-anchor', 'start')
        .style('fill-opacity', 1)
        .style('fill', 'white')
        .style('font-weight', 500)
    },
    createLinks (svg, tier, pdbArray, parentPDB, index) {
      var links = []
      var gap = 0
      var nodes = []
      var output = {}
      var _this = this
      var pdbs
      var verticalMargin = 40

      if (index !== undefined) {
        pdbs = pdbArray[index]
        pdbs = _this.reversePdbArray(pdbs)
      } else {
        pdbs = pdbArray
      }
      if (tier === 'child') {
        gap = 60
      }
      var nodeStartY = h - verticalMargin
      // Configure node 0
      nodes.push([10, nodeStartY])

      // Configure all other nodes
      for (let i = 0; i < pdbs.length; i++) {
        nodes.push([80 + gap, (nodeStartY - i * verticalMargin)])
        nodes[i + 1].push(pdbs[i])
      }
      // Configure link fron node 0 to node 1
      links.push(d3Shape.linkHorizontal()({
        source: nodes[0],
        target: nodes[1]
      })
      )

      // Append connector
      if (pdbs.length > 1) {
        var points = [
          { xpoint: 30 + gap, ypoint: nodeStartY },
          { xpoint: 45 + gap, ypoint: nodeStartY - 10 },
          { xpoint: 47 + gap, ypoint: nodeStartY - 20 }
        ]

        var Gen = d3Shape.line()
          .x((p) => p.xpoint)
          .y((p) => p.ypoint)
          .curve(d3Shape.curveCardinal)

        svg.append('path')
          .attr('d', Gen(points))
          .attr('fill', 'none')
          .attr('class', function () {
            return 'outerElem'
          })
          .attr('stroke', '#76c276')
          .style('stroke-width', 2)

        for (let i = 2; i < nodes.length; i++) {
          var connector = [
            { xpoint: 47 + gap, ypoint: nodes[i][1] + 10 },
            { xpoint: 55 + gap, ypoint: nodes[i][1] },
            { xpoint: 80 + gap, ypoint: nodes[i][1] }
          ]
          svg.append('path')
            .attr('d', Gen(connector))
            .attr('fill', 'none')
            .attr('stroke', '#76c276')
            .style('stroke-width', 2)
            .attr('class', function (d) {
              return 'outerElem'
            })
        }

        svg.append('line')
          .style('stroke', '#76c276')
          .style('stroke-width', 2)
          .attr('x1', 47 + gap)
          .attr('y1', nodeStartY + 10 - (nodes.length - 2) * verticalMargin)
          .attr('x2', 47 + gap)
          .attr('y2', nodeStartY - 20)
          .attr('class', function () {
            if (tier === 1) {
              return 'outerElem'
            } else {
              return 'innerElem'
            }
          })
      }
      // Append the link to the svg element
      svg
        .append('path')
        .attr('d', links)
        .attr('stroke', '#76c276')
        .attr('fill', 'none')
        .style('stroke-width', 2)

      // Append Next and Previous arrows
      if (pdbArray.length > 1) {
        if (index + 1 < pdbArray.length) {
          svg
            .append('circle')
            .attr('cx', 80 + gap)
            .attr('cy', nodeStartY + 30)
            .attr('r', 10)
            .style('fill', '#00b2ff')

          svg.append('image')
            .attr('width', '18px')
            .attr('height', '18px')
            .attr('x', 80 + gap - 9)
            .attr('y', nodeStartY + 21)
            .attr('xlink:href', _this.down)
            .on('click', function () {
              if (index < pdbArray.length - 1) {
                _this.generateArc(pdbArray, parentPDB, index + 1)
              }
            })

          svg.append('text')
            .attr('x', 95 + gap)
            .attr('dy', nodeStartY + 35)
            .attr('class', 'nbr_label')
            .text('Next ' + pdbArray[index + 1].length)
            .attr('text-anchor', 'start')
            .style('fill-opacity', 1)
            .style('fill', 'white')
            .style('font-weight', 500)
        }

        if (index + 1 > 1) {
          svg
            .append('circle')
            .attr('cx', 80 + gap)
            .attr('cy', h - (nodes.length * verticalMargin) + 10)
            .attr('r', 10)
            .style('fill', '#00b2ff')
          // #616161

          svg.append('image')
            .attr('width', '18px')
            .attr('height', '18px')
            .attr('x', 80 + gap - 9)
            .attr('y', h - (nodes.length * verticalMargin) + 1)
            .attr('xlink:href', _this.up)
            .on('click', function () {
              if (index - 1 >= 0) {
                _this.generateArc(pdbArray, parentPDB, index - 1)
              }
            })

          svg.append('text')
            .attr('x', 95 + gap)
            .attr('dy', h - (nodes.length * verticalMargin) + radius)
            .attr('class', 'nbr_label')
            .text('Previous')
            .attr('text-anchor', 'start')
            .style('fill-opacity', 1)
            .style('fill', 'white')
            .style('font-weight', 500)
        }
      }

      // Source node
      svg
        .append('rect')
        .attr('x', 0)
        .attr('y', 400)
        .attr('rx', 6)
        .attr('ry', 6)
        .attr('width', _this.coordinates.width + 2)
        .attr('height', _this.coordinates.height)
        .style('fill', 'orange')
        .attr('class', 'handle')
        .on('click', function () {
          _this.hideTree()
        })

      svg.append('text')
        .attr('x', 3)
        .attr('dy', 415)
        .attr('class', 'nbr_label')
        .text(_this.count)
        .attr('text-anchor', 'start')
        .style('fill-opacity', 1)
        .style('fill', 'white')
        .style('font-weight', 500)

      output.nodes = nodes
      output.links = links
      output.svg = svg
      return output
    },
    waitForElement () {
      var _this = this
      store.commit('setIsCustomFeature', true)
      if (document.getElementById('waitingFrame').style.display !== 'none') {
        setTimeout(_this.waitForElement, 5)
      } else {
        window.AQUARIA.passFeature(window.AQUARIA.customfeatureSet, window.AQUARIA.customfeatureSetioid)
        d3.selectAll('svg.loaded rect.feature').attr('fill', '#a4abdf')
        d3.select('svg.loaded').classed('loaded', false)
      }
    }
  }
}
</script>

<style>
div.scrim {
    background: #5E5E5E;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    opacity: 0.68;
    -moz-opacity: 0.68;
    width: 100%;
}
#arc {
    position: fixed;
    /* top: calc(80vh - 290px); */
    left: 16px;
    /* bottom: 2vh; */
}
.node {
    cursor: pointer;
}

.node circle {
    fill: #fff;
    stroke: steelblue;
    stroke-width: 3px;
}

svg text {
    font: 100% sans-serif;
    /* font-size: calc(0.8rem + 3 * ((100vw - 320px) / 680)); */
    font-size: 12px;
}

.link {
    fill: none;
    stroke: #ccc;
    stroke-width: 2px;
}
</style>
