<script>
import TreeView from '../MatchingStructures/TreeView'
import ExpandableTextLine from 'vue-expandable-text-line'
import NodeLoader from './NodeLoader'
import store from '../../store/index'
import { mapGetters } from 'vuex'
import axios from 'axios'
import * as panel3d from '../StructureViewer/helpers/joleculeAPI'

export default {
  name: 'PopUp',
  components: {
    TreeView,
    ExpandableTextLine,
    NodeLoader
  },
  watch: {
    popupCoordinates: function (newVal) {
      var _this = this
      this.setDefault() // Revert to default style
      if (newVal !== null || newVal !== 0) { // Hide popup when no coordinates passed
        if (typeof (store.state.popupText) === 'object') {
          _this.handleVariantPopup() // Handle popup cases for variants
          _this.togglePinBehaviour()
        } else {
          _this.isVariant = false
        }
        this.PopupPos = newVal // Store residue coordinates to update popup position when popup body updated
      } else {
        _this.hidePopup()
      }
    }
  },
  data () {
    return {
      popupTail: require('../../assets/img/popupTail.png'),
      oneAaCodes: ['A', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W', 'Y'],
      threeAaCodes: ['Ala', 'Cys', 'Asp', 'Glu', 'Phe', 'Gly', 'His', 'Ile', 'Lys', 'Leu', 'Met', 'Asn', 'Pro', 'Gln', 'Arg', 'Ser', 'Thr', 'Val', 'Trp', 'Tyr'],
      isVariant: false,
      variant: null,
      defaultDesc: null,
      treeCoordinates: {},
      change: 0,
      showTree: false,
      isShowTreeViewClicked: false,
      prop: null,
      showLoader: false,
      count: 0,
      PopupPos: null,
      pos: [0, 0, 0, 0],
      hasVariant: false,
      hasResidue: false,
      parentElem: '#popupshell'
    }
  },
  computed: {
    popupCoordinates () {
      return store.state.popupCoordinates
    },
    data () {
      return {
        variantStructs: store.state.variantStructs,
        variantResidues: store.state.variantResidues,
        popupTitle: store.state.popupTitle,
        popupText: store.state.popupText,
        residue: store.state.residue,
        popupCoordinates: store.state.popupCoordinates
      }
    },
    ...mapGetters([
      'getGene',
      'getTotalPDBres',
      'getVariant',
      'getVariantStructs',
      'getVariantResidues',
      'getPopupTitle',
      'getPopupText',
      'getServersDone',
      'getVariantIdx',
      'getMatches',
      'getStructure',
      'getInfoPopup',
      'getIsDefaultFeature'
    ]),
    getTotalPdbCount () {
      var _this = this
      if (_this.isVariant) {
        let totalNumStructs = 0
        for (let i = 0; i < _this.oneAaCodes.length; i++) {
          if (_this.getVariantStructs && _this.getVariant && Object.hasOwnProperty.call(_this.getVariantStructs, _this.getVariant) && Object.hasOwnProperty.call(_this.getVariantStructs[_this.getVariant], _this.oneAaCodes[i]) && Object.hasOwnProperty.call(_this.getVariantStructs[_this.getVariant][_this.oneAaCodes[i]], 'pdbs')) {
            totalNumStructs = totalNumStructs + _this.getVariantStructs[_this.getVariant][_this.oneAaCodes[i]].pdbs.length
          }
        }
        return totalNumStructs
      } else {
        return _this.getTotalPDBres
      }
    }
  },
  // Fix click event functions
  created () {
    var _this = this
    window.addEventListener('click', (e) => {
      if (!_this.$el.contains(e.target) && e.target.parentElement && _this.getPopupTitle) {
        if (!e.target.parentElement.id.includes('tree')) {
          const thePin = document.getElementById('loadedPin')
          if (thePin.classList.contains('loadedPin')) {
            const closeBtn = document.getElementById('x_close')
            closeBtn.click()
          }
        }
      }
    })
  },
  methods: {
    setPopupPos (coordinates) {
      const ptail = document.querySelector('#popupTail')
      const pbody = document.querySelector('#popupbody')
      const pshell = document.querySelector('#popupshell')
      if (this.getPopupTitle.includes('Dark')) { // position popup tail on the line
        coordinates.y = coordinates.y + 20
      }
      var tailPosition = coordinates.x - (ptail.getBoundingClientRect().width / 2) + (coordinates.width / 2)
      ptail.style.left = tailPosition + 'px'
      if (coordinates.height <= 2) { // This is for popup position in dark regions
        pshell.style.top = (coordinates.y - coordinates.height - ptail.getBoundingClientRect().height) + 'px'
      } else if (coordinates.height >= 18) {
        pshell.style.top = (coordinates.y - coordinates.height + 5) + 'px'
      } else {
        pshell.style.top = (coordinates.y - coordinates.height) + 'px'
      }
      pshell.style.left = (tailPosition - 10) + 'px'
      /** Handle right edge cases */
      var popupbodyPos = pshell.getBoundingClientRect().x + pbody.getBoundingClientRect().width
      if ((popupbodyPos) > window.innerWidth) {
        // Shift popupshell to the left. Add 10px to offset any gaps.
        pshell.style.left = (window.innerWidth - pbody.getBoundingClientRect().width + 7) + 'px'
      }
      /** Handle left edge cases */
      if (pbody.getBoundingClientRect().x < 0) {
        pshell.style.left = '-10px'
      }
      /** Handle top edge cases */
      if (pbody.getBoundingClientRect().y < 0) {
        if (!ptail.classList.contains('topTail')) {
          ptail.classList += ' topTail'
        }
        pbody.style.top = ptail.getBoundingClientRect().height - 2 + 'px'
      } else {
        if (ptail.classList.contains('topTail') && (ptail.getBoundingClientRect().y - pbody.getBoundingClientRect().height > 0)) {
          ptail.classList.remove('topTail')
          pbody.style.top = null
        }
      }
    },
    hidePopup () {
      // setTimeout(function () { document.getElementById('popupshell').classList.add('fade') }, 3000)
      var _this = this
      if (!this.isShowTreeViewClicked) {
        store.commit('setPopupTitle', '')
      } else {
        this.isShowTreeViewClicked = false
      }
      if (this.getVariantStructs) {
        _this.resetToInitVar()
      }
    },
    setDefault: function () {
      // Sets default popup style
      const thePin = document.getElementById('loadedPin')
      if (thePin.classList.contains('loadedPin_active')) {
        thePin.classList.remove('loadedPin_active')
        thePin.classList.add('loadedPin')
      }
      const popupTail = document.getElementById('popupTail')
      if (popupTail.classList.contains('hide')) {
        popupTail.classList.remove('hide')
        popupTail.classList.add('show')
      }

      document.getElementById('popupshell').classList.remove('fade')
    },
    handleVariantPopup: function () {
      // Enable amino acid buttons
      // show default variant text
      this.isVariant = true
      this.variant = this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase()
      this.defaultDesc = this.getDefaultDesc()
      this.handleInitAa()
    },
    showTreeView (event, aaCode, residue) {
      this.isShowTreeViewClicked = true
      var _this = this
      var cord = event.srcElement.getBoundingClientRect()
      _this.treeCoordinates.clientX = cord.left
      _this.treeCoordinates.clientY = cord.top
      _this.treeCoordinates.width = cord.width
      _this.treeCoordinates.height = cord.height
      _this.change = _this.change + 1
      _this.showLoader = true
      if (_this.getVariantStructs && Object.hasOwnProperty.call(_this.getVariantStructs, _this.getVariant)) {
        if (aaCode) {
          _this.count = _this.getPdbCount(aaCode)
        } else {
          _this.count = _this.getTotalPdbCount
        }

        var url = `${window.BACKEND}/secondaryClusteringByResidue`
        // var url = `${window.BACKEND}/get_secondary_clusters/P16277/5zj6/A/1`
        var loadRequest = {}
        loadRequest.selector = [_this.getStructure.accession]
        loadRequest.sortedKeys = residue ? [parseInt(residue)] : [_this.getVariant]
        if (aaCode) {
          loadRequest.aaCode = aaCode
        }
        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          console.log(response.data)
          // pdbs = response.data
          _this.showTree = true
          _this.prop = response.data
        })
      } else {
        url = `${window.BACKEND}/secondaryClusteringByResidue`
        // var url = `${window.BACKEND}/get_secondary_clusters/P16277/5zj6/A/1`
        _this.count = _this.getTotalPdbCount
        loadRequest = {}
        loadRequest.selector = [_this.getStructure.accession]
        var innerText = event.srcElement.parentElement.innerText.split(':')
        if (innerText.length > 2) {
          loadRequest.sortedKeys = [parseInt(innerText[1].split('residue ')[1])]
        } else {
          loadRequest.sortedKeys = [parseInt(innerText[0].split('residue ')[1])]
        }
        axios({
          method: 'get',
          url: url,
          params: loadRequest
        }).then(function (response) {
          console.log(response.data)
          // pdbs = response.data
          _this.showTree = true
          _this.prop = response.data
        })
      }
      return 0
    },
    getOldAa: function () {
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'oldAa')) {
        return this.getVariantResidues[this.getVariant].oldAa
      }
      return ''
    },
    highlightAa (aa) {
      this.variant = aa.toUpperCase()
      for (let i = 0; i < this.oneAaCodes.length; i++) {
        document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).classList.remove('selCol')
      }
      document.getElementById('btnVarInfo_' + aa.toUpperCase()).classList += ' selCol'
      if (document.querySelector('#varSpe_toRep')) {
        document.querySelector('#varSpe_toRep').innerText = aa
      }
      if (document.querySelector('#newAa')) {
        var idx = this.oneAaCodes.indexOf(aa)
        document.querySelector('#newAa').innerText = this.threeAaCodes[idx]
      }
    },
    getIfBoldInfoPres: function (aa) {
      let isMakeBold = false
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], aa)) {
        for (let i = 0; i < this.getVariantResidues[this.getVariant][aa].length; i++) {
          if (/* this.getVariantResidues[this.getVariant][aa][i].match('UniProt') || this.getVariantResidues[this.getVariant][aa][i].match('FunVar') || this.getVariantResidues[this.getVariant][aa][i].match('COSMIC') || */ (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && this.getVariantStructs[this.getVariant][aa].pdbs.length > 0)) {
            isMakeBold = true
          }
        }
      }
      if (this.getVariantResidues && this.getVariant > 0) {
        if (isMakeBold === true) {
          if (aa.toUpperCase().includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
            return 'btnAaBold_b selCol'
          } else {
            return 'btnAaBold_b'
          }
        } else {
          if (aa.toUpperCase().includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
            return 'btnAaBold selCol'
          } else {
            return 'btnAaBold'
          }
        }
      }
    },
    getAaDataAsArr: function (aa) {
      var arrForExpandable = []
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], aa)) {
        for (let i = 0; i < this.getVariantResidues[this.getVariant][aa].length; i++) {
        }
        return this.getVariantResidues[this.getVariant][aa]
      } else {
        return arrForExpandable
      }
    },
    resetToInitVar: function () {
      // console.log('Reset to init variant')
      // 1. Update userSpecified feature str.
      if (document.getElementById('urlVariant') && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'theOrigFeatStr') && this.getVariantIdx > -1) {
        document.getElementById('urlVariant').innerHTML = this.getVariantResidues[this.getVariant].theOrigFeatStr[this.getVariantIdx]
      }
      // 2. Update consequenct str
      if (document.getElementById('consInfo') && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'consequent') && this.getVariantIdx > -1) {
        document.getElementById('consInfo').innerHTML = this.getVariantResidues[this.getVariant].consequent[this.getVariantIdx]
      }
      // 3. Update div
      // 4. Update buttons at bottom & span_missense at top.
      this.handleInitAa()
      if (document.getElementById('spanDefaultDesc')) {
        document.getElementById('spanDefaultDesc').hidden = false
      }
      if (this.isHrHidden() === true) {
        document.getElementById('anAaHr').hidden = true
      }
    },
    getVarDisplay: function () {
      if (typeof this.getVariant === 'string' && this.getVariant.match('-')) {
        return this.getVariant.split('-')[0]
      } else {
        return this.getVariant
      }
    },
    getIsShownVar: function (aa) {
      if (this.getMatches && Object.hasOwnProperty.call(this.getMatches, 'Selected_PDB') && Object.hasOwnProperty.call(this.getMatches.Selected_PDB, 'pdb_id') && Object.hasOwnProperty.call(this.getMatches.Selected_PDB, 'pdb_chain')) {
        const key = this.getMatches.Selected_PDB.pdb_id + ':' + this.getMatches.Selected_PDB.pdb_chain

        if (this.getVariant && this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && this.getVariantStructs[this.getVariant][aa].pdbs.includes(key)) {
          return 'selCol_padded'
        } else if (this.getVariant && this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs') && !this.getVariantStructs[this.getVariant][aa].pdbs.includes(key)) {
          return 'noSelCol_padded'
        } else {
          return ''
        }
      }
    },
    getIsShownRes: function () {
      console.log('The variant structs is ')
      console.log(this.getVariantStructs)
      const arr1 = location.href.split('?')
      const arr2 = arr1[0].split('/')

      console.log('This is ... ' + arr2[arr2.length - 2] + ':' + arr2[arr2.length - 1] + ' ' + this.variant + ' ' + this.getVariant)

      const thePdb = arr2[arr2.length - 2] + ':' + arr2[arr2.length - 1]

      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant)) {
        const keys = Object.keys(this.getVariantStructs[this.getVariant])

        let isFound = false
        for (let i = 0; i < keys.length; i++) {
          if (Object.prototype.hasOwnProperty.call(this.getVariantStructs[this.getVariant][keys[i]], 'pdbs') && this.getVariantStructs[this.getVariant][keys[i]].pdbs.includes(thePdb)) {
            isFound = true
            return 'selCol_padded'
          }
        }

        if (isFound === false) {
          return 'noSelCol_padded'
        }
      }
      /*
      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], 'isShown') && this.getVariantStructs[this.getVariant].isShown === true) {
        return 'selCol_padded'
      } else if ((this.getVariantStructs && this.getVariant > -1 && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], 'isShown') && this.getVariantStructs[this.getVariant].isShown === false) || this.getTotalPdbCount !== 0) {
        return 'noSelCol_padded'
      } else {
        return ''
      }
      */
    },
    getPdbCount: function (aa) {
      // console.log(store.getters.getVariant)
      // return 100// store.getters.getPdbCount(aa)
      if (this.getVariantStructs && Object.hasOwnProperty.call(this.getVariantStructs, this.getVariant) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant], aa) && Object.hasOwnProperty.call(this.getVariantStructs[this.getVariant][aa], 'pdbs')) {
        return this.getVariantStructs[this.getVariant][aa].pdbs.length
      }
      return 0
    },
    isHrHidden: function () {
      const defaultDesc = this.getDefaultDesc()
      // const divPopupTextVar = document.getElementById('popupText_variant')
      let isAnAa = false
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'newAas') && this.getVariantIdx > -1 && this.oneAaCodes.includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
        isAnAa = true
      }

      if (defaultDesc === '' && isAnAa === false) {
        // console.log('Num children count is: ') // + // divPopupTextVar.children.length)
        return true
      } else {
        return false
      }
    },
    getDefaultDesc: function () {
      // console.log('Restaurant ' + this.getVariantIdx + ' ' + this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx])

      if (this.getVariant && this.getVariantResidues && Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'defaultDesc') && this.getVariantIdx > -1) {
        return this.getVariantResidues[this.getVariant].defaultDesc[this.getVariantIdx]
      } else {
        return ''
      }
    },
    getPosInfoDataAsArr: function () {
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'positionInfo')) {
        return this.getVariantResidues[this.getVariant].positionInfo
      }
      return []
    },
    handleInitAa: function () {
      var _this = this
      console.log('handleInitAa runs ')//  + this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx])
      console.log(this.getVariantIdx)
      // console.log('handleInitAa runs (2) ' + this.getVariantResidues[this.getVariant].newAas + ' ' + this.getVariantIdx)
      if (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'newAas') && this.getVariantIdx > -1 && this.oneAaCodes.includes(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())) {
        _this.highlightAa(this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())
        // const aaBtn = document.getElementById('btnVarInfo_' + this.getVariantResidues[this.getVariant].newAas[this.getVariantIdx][0].toUpperCase())
        // aaBtn.click()
      } else { // Hide all the divs
        const oldAa = this.getOldAa()
        for (let i = 0; i < this.oneAaCodes.length; i++) {
          const theDiv = document.getElementById('divVarInfo_' + this.oneAaCodes[i])
          if (typeof (theDiv) !== 'undefined' && theDiv !== null) {
            document.getElementById('divVarInfo_' + this.oneAaCodes[i]).hidden = true
            document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).classList.remove('selCol')
          }
          // Hide oldAa btn
          if (oldAa === this.oneAaCodes[i]) {
            // document.getElementById('btnVarInfo_' + this.oneAaCodes[i]).hidden = true
          }
        }
        _this.updateSpanAa('')
      }
    },
    updateSpanAa: function (valToDisp) {
      console.log('Value to display is ' + valToDisp + this.getVariantIdx)
      if (document.getElementById('span_missenseHeading')) {
        /*
        let a = document.getElementById('span_missenseHeading').innerHTML
        a = a.replace(/[A-Z]*\)$/, valToDisp + ')')
        document.getElementById('span_missenseHeading').innerHTML = a
        */

        if (this.getVariantResidues && (Object.hasOwnProperty.call(this.getVariantResidues, this.getVariant) && Object.hasOwnProperty.call(this.getVariantResidues[this.getVariant], 'isOne') && this.getVariantIdx > -1 && this.getVariantResidues[this.getVariant].isOne[this.getVariantIdx] === true)) {
          // specify as 3 letters.
          const idx = this.oneAaCodes.indexOf(valToDisp)
          if (idx !== -1) {
            document.getElementById('newAa').innerHTML = this.threeAaCodes[idx]
          } else {
            document.getElementById('newAa').innerHTML = ''
          }
        } else {
          document.getElementById('newAa').innerHTML = valToDisp
        }
        // console.log('the heading is updating here ' + valToDisp)
      }
    },
    togglePinBehaviour: function () {
      // 1. Get pin status.
      const thePin = document.getElementById('loadedPin')

      if (thePin.classList.contains('loadedPin_active')) {
        thePin.classList.remove('loadedPin_active')
        thePin.classList.add('loadedPin')
      } else if (thePin.classList.contains('loadedPin')) {
        thePin.classList.remove('loadedPin')
        thePin.classList.add('loadedPin_active')
      }
    },
    // The code below uses examples from https://www.w3schools.com/howto/howto_js_draggable.asp
    // Used to make the popup body draggable
    dragMouseDown: function (e) {
      e = e || window.event
      e.preventDefault()
      // get the mouse cursor position at startup:
      this.pos[2] = e.clientX
      this.pos[3] = e.clientY
      document.onmouseup = this.closeDragElement
      // call a function whenever the cursor moves:
      document.onmousemove = this.elementDrag
    },
    elementDrag: function (e) {
      e = e || window.event
      e.preventDefault()
      var elmnt = document.getElementById('popupshell')
      // calculate the new cursor position:
      this.pos[0] = this.pos[2] - e.clientX
      this.pos[1] = this.pos[3] - e.clientY
      this.pos[2] = e.clientX
      this.pos[3] = e.clientY
      // set the element's new position:
      elmnt.style.top = (elmnt.offsetTop - this.pos[1]) + 'px'
      elmnt.style.left = (elmnt.offsetLeft - this.pos[0]) + 'px'

      const popupTail = document.getElementById('popupTail')
      if (popupTail.classList.contains('show')) {
        popupTail.classList.remove('show')
        popupTail.classList.add('hide')
      }
    },
    closeDragElement: function () {
      // stop moving when mouse button is released:
      document.onmouseup = null
      document.onmousemove = null
    },
    getResInfo: function () {
      var jAlignment = panel3d.joleculeAlignment
      var jController = panel3d.embededJolecule.controller
      var pdbResNum = jAlignment.mapSeqResToPdbResOfChain(0, this.getVariant, this.getStructure.chain)
      if (pdbResNum) {
        var iRes = jController.soup.findResidueIndices(0, pdbResNum[1], pdbResNum[2])
        var structAa = jController.soup.getResidueProxy().load(iRes).resType
        structAa = structAa.substr(0, 1) + structAa.substr(1).toLowerCase()
        return this.getGene + ' residue ' + this.getVariant + '<span style="font-weight: 400; color: #a6a5a6;"> (' + this.getStructure.pdb + '-' + this.getStructure.chain + ' residue ' + structAa + '-' + pdbResNum[2] + ')'
      } else if (document.querySelector('[id^="residue_' + this.getVariant)) {
        return this.getGene + ' residue ' + this.getVariant + '<span style="font-weight: 400; color: #a6a5a6;">'
      } else {
        return this.getGene + ' residue ' + this.getVariant + '<span style="font-weight: 400; color: #a6a5a6;"> ( Dark region )'
      }
    }
  },
  updated () {
    // Set popup position after popup body updated
    if (this.PopupPos !== null) {
      this.setPopupPos(this.PopupPos)
    }
  }
}
</script>

