<template>
    <div id="title" class="item title fix level5">
      <span id="pdbChain" v-if="!getStructure.accession">
        <a href="#Structure" class="pdbPanel" @click="makeActive('pdbPanel')">
          <img v-bind:src="search"> PDB <strong>{{getStructure.pdb}}</strong>
        </a>
        <!-- <a href="" class="pdbPanel" @click="makeActive('pdbPanel')" v-if="!getOrganism && !seqRes">
          <img v-bind:src="search">
          PDB Structure
        </a> -->
        <span id='help' @click="showHelp" :style="{visibility: helpOption ? 'visible' : 'hidden'}">?</span>
      </span>

      <span id="org_prot" v-if="getStructure.accession">
        <a href="#Sequence" class="seqPanel" @click="makeActive('seqPanel')" v-if="getOrganism && !seqRes">
          <img v-bind:src="search">
          {{ getOrganism.replaceAll('_', ' ') }} <strong>{{getGene}}</strong>
        </a>
        <a href="" class="seqPanel" @click="makeActive('seqPanel')" v-if="!getOrganism && !seqRes">
          <img v-bind:src="search">
          Protein Sequence
        </a>
      </span>

      <span id="alignment" v-if="!seqRes && getStructure.accession">
          <a href="#Alignment" class="alignPanel" @click="makeActive('alignPanel')" >aligned onto</a>
      </span>

      <span id="struct" v-if="!seqRes && getStructure.accession">
        <a href="#Structure" class="structPanel" @click="makeActive('structPanel')" v-if="!getStructure.pdb">PDB ID</a>
        <a href="#Structure" class="structPanel" @click="makeActive('structPanel')" v-if="getStructure.pdb && !seqRes">{{getStructure.pdb + '-' + getStructure.chain}}</a>
        <span id='help' @click="showHelp" :style="{visibility: helpOption ? 'visible' : 'hidden'}">?</span>
      </span>

      <!-- <span id='selection' v-if="seqRes" class="selection">
        <a href="#Alignment" class="selection selectionPanel" @click="makeActive('selectionPanel')" >
          <span class="selection"> <strong>{{getGene}}</strong> sequence: </span><span class="selection">{{seqRes}} </span><br/>
          <span class="selection"><strong>{{getStructure.pdb}}</strong> structure: </span><span class="selection">{{structRes}}</span>
        </a>
      </span> -->

      <div id='titleAlign' v-if="seqRes && getStructure.accession">
        <span class='titlepanel' v-on:click="storeHash">
          <a href="#Alignment" class="selectionPanel" @click="makeActive('selectionPanel')">
            <strong>{{getGene}}</strong> sequence: {{seqRes}} <br/>
            <strong>{{getStructure.pdb}}</strong> structure: {{structRes}}
            </a>
        </span>
      </div>
      <a  v-bind:href="data.hash" class="close" @click="dismissPanel"></a>
    </div>
</template>

<script>
import store from '../../store/index'
import * as Panels from '../AquariaLayout/helpers/hidePanels'
import * as panel3d from '../StructureViewer/helpers/joleculeAPI'
import { mapGetters } from 'vuex'
import { findPDBMember } from '../../utils/loadData'

export default {
  name: 'Title',
  data () {
    return {
      pdb: null,
      search: require('../../assets/img/search_100.png'),
      seqRes: null,
      structRes: null,
      helpOption: true
    }
  },
  computed: {
    data () {
      return {
        hash: store.state.hash
      }
    },
    scrim () {
      return store.state.showScrim
    },
    ...mapGetters([
      'getHash',
      'getOrganism',
      'getGene',
      'getStructure'
    ])
  },
  watch: {
    getStructure: function (newVal) {
      if (store.state.urlParams) {
        var urlParams = store.state.urlParams
      } else {
        urlParams = window.location.href.substr(window.location.origin.length + window.location.pathname.length)
      }
      var _this = this
      if (this.getGene && this.getOrganism && this.getStructure && newVal) {
        history.pushState(null, document.title, '/' +
      _this.getOrganism + '/' + _this.getGene + '/' + _this.getStructure.pdb + '/' + _this.getStructure.chain + urlParams)
      }
    },
    getOrganism: function (newVal) {
      if (store.state.urlParams) {
        var urlParams = store.state.urlParams
      } else {
        urlParams = window.location.href.substr(window.location.origin.length + window.location.pathname.length)
      }
      var _this = this
      if (this.getGene && newVal) {
        history.pushState(null, document.title, '/' +
      newVal + '/' + _this.getGene + '/' + _this.getStructure.pdb + '/' + _this.getStructure.chain + urlParams)
      }
    },
    getGene: function (newVal) {
      var _this = this
      if (store.state.urlParams) {
        var urlParams = store.state.urlParams
      } else {
        urlParams = window.location.href.substr(window.location.origin.length + window.location.pathname.length)
      }
      if (this.getOrganism && newVal) {
        history.pushState(null, document.title, '/' +
      _this.getOrganism + '/' + newVal + '/' + _this.getStructure.pdb + '/' + _this.getStructure.chain + urlParams)
      }
    },
    scrim: function (newVal) {
      var _this = this
      if (!newVal) { // And error message is null
        _this.dismissPanel()
      }
    }
  },
  methods: {
    showHelp: function () {
      store.commit('setHelpTitle', 'Aquaria Help')
      store.commit('setUserStatus', false)
      document.getElementById('UserHelp').classList.remove('hide')
      document.getElementById('UserHelp').className += (' show')
      // document.querySelector('#UserHelp').style.display = 'flex'
      // document.querySelector('.main .dimmer').style.display = 'block'
      if (!document.querySelector('div.dimmer')) {
        Panels.overlay()
        document.querySelector('div.dimmer').className += ' level7'
      }
      document.querySelector('div.dimmer').addEventListener('click', function () {
        Panels.RemoveOverlay()
        document.getElementById('UserHelp').classList.remove('show')
        document.getElementById('UserHelp').className += (' hide')
      })
    },
    storeHash: function () {
      if (window.location.hash === '') {
        store.commit('setHash', '#(0,0,0)')
      } else {
        if (window.location.hash.includes('-') || window.location.hash.includes('(')) {
          store.commit('setHash', window.location.hash)
        }
      }
    },
    makeActive: function (className) {
      this.helpOption = false
      this.storeHash()
      document.querySelectorAll('#title span a').forEach(el => {
        el.classList.remove('active')
      })
      // if (ev.target.localName.includes('strong')) {
      //   ev.target = ev.target.parentElement
      // }
      document.querySelector('.' + className).classList += ' active'
      // ev.target.className = 'active'
      if (document.querySelector('#title').className.indexOf('active') === -1) { document.querySelector('#title').className += ' active' }
      document.querySelector('#scrim').className = 'show level2'
      store.commit('setScrim', true)
      if (document.querySelector('#titleAlign') !== null) {
        document.querySelector('#titleAlign').className = 'active'
        store.commit('setAlignment', panel3d.joleculeAlignment.copyToClipboard())
        store.commit('setHash', window.location.hash)
      }
    },
    dismissPanel: function () {
      // hide scrim
      // document.querySelector('#pdb_id > span#help').style.visibility = 'visible'
      this.helpOption = true
      var scrim = document.getElementById('scrim')
      if (scrim.classList.contains('show')) {
        scrim.classList.remove('show')
        scrim.classList.add('hide')
      }
      // document.querySelector('#scrim').className = 'hide'
      // reset title to neutral state
      store.commit('setHash', this.getHash)
      document.querySelector('#title').className = 'item title fix level6'
      if (document.querySelector('#titleAlign') !== null) { document.querySelector('#titleAlign').className = '' }
      document.querySelectorAll('#title span a').forEach(el => {
        el.classList.remove('active')
      })
    },
    resetSelection: function () {
      document.querySelectorAll('#titlebar span').forEach(el => {
        el.className = 'titlepanel'
      })
      document.querySelectorAll('.contents').forEach(el => {
        el.style.display = 'none'
      })
    },
    release: function () {
      document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection').style.display = 'none'
      this.resetSelection()
    }
  },
  mounted () {
    this.resetSelection()
  },
  updated () {
    var _this = this
    _this.primary_accession = _this.getGene
    var selectedRes = new MutationObserver(function () {
      if (document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection').style.display === 'none') {
        _this.seqRes = null
        _this.structRes = null
        // _this.alignment = null
        store.commit('setAlignment', '')
      } else {
        var PDBIndex = findPDBMember(_this.getStructure.pdb, _this.getStructure.chain)
        var currentMember = store.state.Matches.clusters[PDBIndex.cluster].members[PDBIndex.member]
        var residues = document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection').innerText.split('\n')
        var pdb = currentMember.pdb_id + '[' + currentMember.model + ']-' + currentMember.pdb_chain + ':'
        var accession = _this.getGene + ':'
        _this.seqRes = residues[1].split(accession)[1]
        _this.structRes = residues[0].split(pdb)[1]
      }
    })
    selectedRes.observe(document.querySelector('#threeDSpan-inner-jolecule-soup-display-canvas-wrapper-selection'), {
      attributes: true, childList: true, characterData: true
    })
  }
}
</script>

<style scoped>
