  padding: 0px 20px;
  border-radius: 20px;
}

#struct > span#help, #pdbChain > span#help {
  background: white;
  color: #595656;
  padding: 0px 0.4em;
  border-radius: 1.5rem;
  margin: 2px;
  font-size: 100%;
}
