  background-color: var(--primary-link);
  color: white;
}
#struct > span#help:hover {
  background-color: var(--primary-link);
  color: white;
}
.title {
    flex-basis: auto;
    color: white;
    text-align: center;
    line-height: 1.25;
    padding: 0;
    border-radius: 1.5rem;
    margin: auto;
    left: 50%;
    top: 1.5vh;
    transform: translate(-50%, -5%);
    /* width: fit-content; */
}
#title.active, #titleAlign.active, #titleAlign.active {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  width: fit-content;
  max-width: 29.5rem;
  margin-top: .125rem;
}
#title span {
  background-color: var(--primary-label);
  white-space: nowrap;
  text-align: left;
  flex-shrink: 0;
}
#title.active span#struct, #title.active span#struct {
  flex-grow: 2;
  flex-shrink: 0;
  /* width: 50%; */
}
#title span a {
  display: inline-block;
  background-color: var(--primary-label);
  padding: calc(0.4rem + 3 * ((100vw - 320px) / 680)) 0.2rem;
  transition: all 0.5s ease;
  color: var(--text);
  text-decoration: none;
}
#title span a:hover {
  background-color: var(--primary-link);
}

#title span a:active, #title span a.active {
  background-color: var(--primary-highlight);
}

#title:hover span#alignment a {
  border-right: 1px dotted var(--background);
  border-left: 1px dotted var(--background);
}
span#org_prot, span#uniprotpanel {
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
}
span#org_prot a, span#uniprotpanel a {
  padding-left: 0.5rem;
  border-top-left-radius: 2rem;
  border-bottom-left-radius: 2rem;
  transition: all 0.7s ease;
}
span#org_prot img, span#pdbChain img, span#uniprotpanel img, span#struct img  {
height: calc(10px + .4vw);
}
span#struct , span#struct {
  padding-right: 0.5rem;
  font-weight: 600;
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
}
span#selection {
  padding: 0px 20px;
  border-radius: 20px;
}

#selection:hover .selection{
  background-color: var(--primary-link);
  color: white;
}

#titleAlign {
  display: flex;
  justify-content: center;
}

/* #titleAlign:hover a, #titleAlign:hover span{
  background-color: var(--primary-link);
} */

#titleAlign.active {
  border-radius: 1.5rem;
}
#titleAlign span a {
  padding: calc(0.2rem + 2 * ((100vw - 320px) / 680)) 1rem;
  font-size: 95%;
  line-height: 1rem;
  border-radius: 1.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 15rem;
  text-align: center;
}

#title span.titlepanel {
  background-color: transparent;
  text-align: left;
}

#titleAlign.active span.titlepanel a {
  width: 80vw;
  max-width: 29.5rem;
}
#titleAlign.active + a.close::after {
    top: calc(0.5rem + 3 * ((100vw - 320px) / 680));
}
</style>
