<template>
  <div>
    <div class="tabs rel level3">
      <div class="tab"  id="Structures" v-on:click="renderClusters">
        <a href="#Structures"><span class="icon"><img v-bind:src="MS" /></span>&nbsp;&nbsp;Matching Structures&nbsp;&nbsp;<span class="counter" v-if="getStructCount > 0"> {{ getStructCount.toLocaleString() }}</span></a>
      </div>
      <MatchingStructures />
      <div class="tab" id="Features" v-on:click="renderFeatures">
        <a href="#Features"><span class="icon"><img v-bind:src="FT" /></span>&nbsp;&nbsp;Features&nbsp;&nbsp;<span class="counter" id="featureCounter" v-if="getFeatCount">{{ getFeatCount.toLocaleString() }}</span></a>
      </div>
      <Features />
    </div>
  </div>
</template>
<script>
import MatchingStructures from '../MatchingStructures/MatchingStructures'
import Features from '../Features/Features'
import * as Panels from './helpers/hidePanels'
import store from '../../store/index'
import { mapGetters } from 'vuex'
import * as renderFeature from '../Features/helpers/renderPanel'
import * as showMatchingStructures from '../MatchingStructures/helpers/show_matching_structures'
import * as panel3d from '../StructureViewer/helpers/joleculeAPI'

export default {
  name: 'Tabs',
  components: {
    MatchingStructures,
    Features
  },
  data () {
    return {
      MS: require('../../assets/img/MS-01.svg'),
      FT: require('../../assets/img/FT-01.svg')
    }
  },
  computed: {
    ...mapGetters([
      'getFeatCount',
      'getStructCount',
      'getMatches'
    ])
  },
  methods: {
    storeHash: function () {
      // Store fragment value before it gets replaced
      if (window.location.hash === '') {
        store.commit('setHash', '#(0,0,0)')
      }
      if (window.location.hash.includes('(')) {
        store.commit('setHash', window.location.hash)
      }
    },
    renderClusters: function () {
      this.storeHash()
      // @TODO Rewrite cluster rendering methods.
      if (!document.querySelector('#allclusters div')) {
        showMatchingStructures.initialise(this.$store.state.Matches.sequences[0])
        if (!document.querySelector('.outer_container')) {
          var newClusters = this.getMatches.clusters
          for (var i = 0; i < newClusters.length; i++) {
            showMatchingStructures.RenderCluster(newClusters[i])
          }
          showMatchingStructures.updateSizes(this.getMatches.clusters)
        }
        var clusterNumber = this.getMatches.Selected_PDB.cluster_number
        var cluster = this.getMatches.clusters[clusterNumber]
        showMatchingStructures.selectCluster(cluster.pdb_id, clusterNumber)
      }
    },
    renderFeatures: function () {
      this.storeHash()
      // @TODO Update features panel if sequence changes
      showMatchingStructures.initialise(this.$store.state.Matches.sequences[0])
      renderFeature.updateFeatureUI(store.state.featureSets)
    },
    showPanel: function (elem1, elem2) {
      document.querySelector('#' + elem1).style.display = 'block'
      document.querySelectorAll('.tab').className = 'tab inactive'
      document.querySelector('#' + elem2).className = 'tab active'
    },
    showMatches: function () {
      var _this = this
      if (panel3d) {
        const soupController = panel3d.embededJolecule.controller
        soupController.clearSelectedResidues()
      }
      _this.showPanel('vis', 'Structures')
      Panels.hidePanel('featurePanel', 'Features')
      if (document.getElementsByClassName('dimmer').length === 0) {
        Panels.overlay()
      }
      document.querySelector('div.dimmer').className += ' level0'
      // document.querySelector('div.dimmer').style.zIndex = '0'
      document.querySelector('div.dimmer').addEventListener('click', function () {
        // document.querySelector('div.tabs').style.bottom = '50px'
        Panels.hidePanels()
      })
    },
    showFeatures: function () {
      var _this = this
      if (panel3d) {
        const soupController = panel3d.embededJolecule.controller
        soupController.clearSelectedResidues()
      }
      Panels.hidePanel('vis', 'Structures')
      _this.showPanel('featurePanel', 'Features')
      if (document.getElementsByClassName('dimmer').length === 0) {
        Panels.overlay()
      }
      document.querySelector('div.dimmer').className += ' level0'
      document.querySelector('div.dimmer').addEventListener('click', function () {
        document.querySelector('a.close').click()
        // document.querySelector('div.tabs').style.bottom = '50px'
        Panels.hidePanels()
      })
    }
  },
  mounted () {
    var _this = this
    window.addEventListener('hashchange', function () {
      if (window.location.hash.includes('Structures')) {
        _this.showMatches()
      }
      if (window.location.hash.includes('Features')) {
        _this.showFeatures()
      }
    })
  }
}
</script>
