<template>
    <div class="scrim level6" @click="hideLoader" id='nodeLoader'>
        <img :src='loader' />
    </div>
</template>

<script>
export default {
  name: 'NodeLoader',
  data () {
    return {
      loader: require('../../assets/img/node.gif')
    }
  },
  props: ['coordinates', 'change'],
  watch: {
    change: function (newVal, oldVal) {
      var _this = this
      document.querySelectorAll('#nodeLoader > img').forEach(function (elem) {
        elem.style.marginLeft = _this.coordinates.clientX + 'px'
        elem.style.marginTop = _this.coordinates.clientY - 10 + 'px'
      })
      // document.querySelector('#nodeLoader > img').style.marginLeft = _this.coordinates.clientX + 'px'
      // document.querySelector('#nodeLoader > img').style.marginTop = _this.coordinates.clientY - 10 + 'px'
    }
  },
  methods: {
    hideLoader () {
      this.$parent.showLoader = false
      this.$parent.showTree = false
    }
  }
}
</script>

<style scoped>
img {
    width: 5vh;
    background: #4d4d4d;
    border-radius: 29px;
    padding: 5px;
}
</style>
