import * as TopTenStore from '../../../utils/topten'
import store from '../../../store/index'

require('./utilities')

var MAX_PROTEIN_HISTORY = 10;
var proteinSubmitListeners = []
  var addProteinSubmitListener = function (listener) {
    proteinSubmitListeners.push(listener)
  }

  var proteinTopTen = TopTenStore.TopTen('protein_top_ten', MAX_PROTEIN_HISTORY)
  addProteinSubmitListener(proteinTopTen)
