/* eslint-disable no-tabs */
import ClusterRenderer from './clusterRenderer'
import d3 from 'd3'
import store from '../../../store/index'
import * as loadData from '../../../utils/loadData'
var width, height, xScale, rank, clusters
var clusterRenderers = []

// Render 2D structures in SVG.
//
// Authors: Christian Stolte, Vivian Ho, Kenny Sabir
// Utilises new format (see matching_structures_multipleuniprotinput.js)
// as opposed to matching_structures_singleuniprotinput.js
// new tree drawing routine

function onTextClick (d, clickCoord) {
  // Sets tree data for clusters
  var clusterN = parseInt(d.attr('id').substr(15))
  store.commit('setTreeData', clusterN)
  store.commit('setTreeCoordinates', clickCoord)
}

export function selectCluster (pdb_id, clusterNumber) {
  var pdbMember = []
  pdbMember.cluster = parseInt(clusterNumber)
  pdbMember.member = 0
  store.commit('setPdbIndex', pdbMember)
  if (document.querySelector('g.loaded')) document.querySelector('g.loaded').classList.remove('loaded')
  d3.select('div.container.loaded').classed('loaded', false)
  d3.select('g#structure_' + pdb_id + '_' + clusterNumber).classed('loaded', true)
  d3.select('div#c_' + pdb_id + '_' + clusterNumber).classed('loaded', true)
}

export function initialise (sequence) {
  rank = 0
  var seqLength = sequence.length

  width = document.getElementById('structure-viewer').offsetWidth - store.state.margin.right - store.state.margin.left - 50
  height = 40 - store.state.margin.top - store.state.margin.bottom + 35 // height
  xScale = d3.scale.linear().domain([1, seqLength]).range([1, width]) // .range([1, width]);
  store.commit('setSrw', xScale(100) / 100)
  if (document.querySelector('#vis > .ruler')) {
    document.querySelector('#vis > .ruler').remove()
  }
  // draw axis ruler
  drawAxisRuler('vis')
}

export function updateSizes (newClusters) {
  clusters = newClusters
  for (var i = 0; i < newClusters.length; i++) {
    clusterRenderers[i].updateCluster(newClusters[i])
  }
}

export function refresh () {
  clusterRenderers = []
  clusters = []
  initialise (store.state.Matches.sequences[0])
  var existingClusters = store.state.Matches.clusters
  existingClusters.forEach(function (cluster) {
    RenderCluster(cluster)
  })
  updateSizes(existingClusters)
  selectCluster()
}

export function RenderCluster (cluster) {
  var clusterRenderer = new ClusterRenderer(cluster, rank, xScale, width, height, function (d, clickCoord) {
      console.log('ShowMatchingStructures.addCluster.onclick', clickCoord)
      onTextClick(d, clickCoord)
    }, clusterItemClick, '#allclusters')
  clusterRenderers.push(clusterRenderer)
  rank++
}

function getClusterId (d) {
  return parseInt(d.attr('id').substr(15))
}

function clusterItemClick (d) {
  history.pushState('', document.title, window.location.pathname + window.location.search)
  var shortId = d.attr('id').substr(10, 4)
  var cluster_nbr = getClusterId(d)

  // Load structure
  selectCluster(shortId, cluster_nbr)
  console.log(
    'ShowMatchingStructures.clusterItemClick',
    'shortId: ' + shortId,
    'cluster-number: ' + cluster_nbr,
    clusters[cluster_nbr]
  )

  var member = clusters[cluster_nbr].members[0]
  loadData.displayMember(member)
}

export function drawAxisRuler (layerId) { // console.log("Ruler// "+width+",
  width = document.getElementById('structure-viewer').offsetWidth - store.state.margin.right - store.state.margin.left - 50
  var rsvg = d3.select('#' + layerId)
    .insert('div', ':first-child')
    .attr('class', 'ruler')
    .attr('data-intro', "Graphical representation of the specified protein's sequence")
    .attr('data-position', 'left')
    .append('svg').attr('width',
      parseInt(width + store.state.margin.left)).attr('viewBox',
      '0 0 ' + parseInt(width + store.state.margin.left) + ' 15').attr(
      'preserveAspectRatio', 'none').attr('height', 15).append('g').attr(
      'transform', 'translate(' + store.state.margin.left + ',0)')

  rsvg.append('g').attr('class', 'x axis').call(
    d3.svg.axis()
      .scale(xScale)
      .orient('bottom')
      .tickSize(4, 2, 6)
      .tickPadding(2)
    //	.tickValues([allTicks,this.sequence.length])
  )

  var tickList = []
  var allTicks = d3.selectAll('#vis div.ruler g.x text')[0]

  for (var n = 0; n < allTicks.length; n++) {
    tickList.push(+allTicks[n].textContent)		// console.log(n+":"+tickList);
  }

  tickList = tickList.filter(doesItFit) // console.log("filtered:"+tickList);
  tickList.push(store.state.Matches.sequences[0].length)
  tickList.unshift(1)
  // left
  // console.log(" = = = ticks: "+allTicks)	;																	// margin:
  // console.log(tickList);

  d3.select('#' + layerId + ' div.ruler g.x').call(
    d3.svg.axis()
      .scale(xScale)
      .orient('bottom')
      .tickSize(4, 2, 6)
      .tickPadding(2)
      .tickValues(tickList)
  )

  d3.selectAll('g.x text').attr('text-anchor', 'end')
}

function doesItFit (x) {
  var rulerEnd = store.state.Matches.sequences[0].length
  return (x !== 1 && x !== rulerEnd && x * store.state.srw < (rulerEnd * store.state.srw - 30))
}