import d3 from 'd3'
import * as panels from '../../AquariaLayout/helpers/hidePanels'
import store from '../../../store/index'
import * as viewer from './loadFeature'
import * as popup from './handleFeaturePop'
import * as showMatchingStructures from '../../MatchingStructures/helpers/show_matching_structures'
var featureSetCount = 0
var width = 0

export function updateFeatureUI (data) {
  if (document.querySelector('#featurePanel > .ruler')) {
    document.querySelector('#featurePanel > .ruler').remove()
  }
  /**
   * @todo
   * Resize tracks on resize
   */
  showMatchingStructures.drawAxisRuler('featurePanel')
  data.forEach(function (key) {
    var featureHeader = d3.select('#' + key[0].replace(/\s/g, '\\ '))
    var fh = featureHeader.select('.featureTrack').selectAll('div')
    for (var aFsIdx in key[1]){
      if (((key[0] != 'Added Features') || (!key[1][aFsIdx].hasOwnProperty('SeqHash')) || (key[1][aFsIdx].SeqHash == '') || (key[1][aFsIdx].SeqHash == store.state.Matches.sequences[0].uniprot_hash))) {
        fh.data([key[1][aFsIdx]])
        .enter()
        .append('div') 
        .attr('class', 'track')
        .attr('id', function(d){
          return 'track_' + d.Category
        })
        .html(
          function (d) {
            if (d.Type != '') {
              if (d.Server == 'UniProt' || d.Server == 'PredictProtein' || d.Server == 'CATH' || d.Server == 'SNAP2' || d.Server == 'COSMIC' || d.Server == 'FunVar') {
                    var source = 'Curated Features'
              } else {
                  source = d.Server
              }
              return "<p><span class='featureSetName'>" +
                    d.Category +
                    '</span> <i class="featureSetName_">(' +
                    d.Type.replace(/_/g, ' ') +
                    ")</i> &nbsp;&nbsp; <span class='info'>?</span>  <span class='source'>" +
                    source.replace(/[sS]$/, '') +
                    '</span> </p> <span class=\'tooltiptext\'>' + d.Description + '</span>'
            } else {
              if (d.Server == 'UniProt' || d.Server == 'PredictProtein' || d.Server == 'CATH' || d.Server == 'SNAP2' || d.Server == 'COSMIC' || d.Server == 'FunVar') {
                source = 'Curated Features'
            } else {
                source = d.Server
            }
              return '<p><span class="featureSetName">' +
                    d.Category +
                    "</span> &nbsp;&nbsp; <span class='info'>?</span> <span class='source'>" +
                    source.replace(/[sS]$/, '')  +
                    '</span></p> <span class=\'tooltiptext\'>' + d.Description + '</span>'
            }
          }
        )
        .each(drawTrack)
      }
    }
  })
}

/** 
 * @TODO
 * draw track using document fragment
 */
export function drawTrack (datum, i) {
    var features = datum.Tracks
    for (var track in features) {
      var outerdiv = d3.select(this)
      var nusvg = createSVGforFeature(outerdiv)
      nusvg
        .attr('id', 'ftrack_' + featureSetCount + '_' + track)  
        .on('click', function () {
          if (d3.select(this).attr('class') == 'loaded') { // deselect feature (it's already displayed)
            /** 
             * @TODO
             * Improve functions to:
             * 1. Deselect feature track
             * 2. Collapse feature set
            */
            store.commit('setLoadedFeature', {set: null, track: null})
            d3.select('svg.loaded').classed('loaded', false)
            document.querySelector('.featureHeader').click()
            viewer.removeFeature()
            popup.hidePopup()
            panels.hidePanels()
          } else {
            /** 
             * @TODO
             * Use vuex to remove or update feature map
             * Use vuex to detect if loaded feature is an user-defined feature
            */
            if (document.querySelector('#outerFeatureMap')) {
              document.querySelector('#outerFeatureMap').remove()
            }
            if (datum.Server != 'Added Features') {
              store.commit('setIsCustomFeature', false)
            }
            // Store selected feature in vuex store
            var oid = d3.select(this).attr('id').split('_')[2]
            store.commit('setLoadedFeature', {set: datum, track: oid})

            d3.selectAll('svg.loaded rect.feature').attr('fill', '#a4abdf')
            d3.select('svg.loaded').classed('loaded', false)
            d3.select(this).attr('class', 'loaded')
            panels.hidePanels()
          }
        })
        .on('mouseover', function () {
          d3.select(this).selectAll('rect.feature').attr(
            'fill', function () {
              return d3.select(this).attr('color')
            })
        })
        .on('mouseout', function () {
          if (d3.select(this).attr('class') !== 'loaded') {
            d3.select(this).selectAll('rect.feature')
              .attr('fill', '#a4abdf')
          }
        })
      // add background for highlighting
        .append('rect')
        .attr('width', width + store.state.margin.left)
        .attr('height', 25)
        .attr('class', 'bg')
        .attr('fill', 'none')
  
      d3.select('svg#ftrack_' + featureSetCount + '_' + track)
        .append('g')
        .attr('transform', 'translate(' + store.state.margin.left + ',0)')
      // add center line
        .append('rect')
        .attr('width', width)
        .attr('height', 1)
        .attr('transform', 'translate(0,13)')
        .attr('class', 'insertion')
      
        drawSingleFeature(datum, features, track)
    }
    featureSetCount++
}

export function drawSingleFeature (datum, features, track) {
  for (var feature in features[track]) {
    var featureCount = store.state.featCount + 1
    store.commit('setFeatCount', featureCount)
    d3
      .select('#ftrack_' + featureSetCount + '_' + track + ' g')
      .append('rect')
      .attr('width', function () {
        return (/* parseInt */((features[track][feature].size + 1) * store.state.srw) > 2) ? /* parseInt */((features[track][feature].size + 1) * store.state.srw) : 2
      })
      .attr('height', 14)
      .attr('id', 'feature_' + featureSetCount + '_' + track + '_' + feature)
      .attr('transform', 'translate(' + /* parseInt */ (features[track][feature].start * (store.state.srw - 0.0085)) + ',6)')
      .attr('color', features[track][feature].color).attr('fill', '#a4abdf')
      .attr('fill-opacity', function () { return (datum.Class == 'single_track') ? 0.3 : 1 })
      .attr('class', 'feature')
      .on('mouseover', function () {
        d3.select(this).attr('stroke-width', '2px').attr('stroke', 'white')
        popup.showPopup(features[this.id.split('_')[2]][this.id.split('_')[3]], this)
      })
      .on('mouseout',	function () {
        d3.select(this).attr('stroke-width', '0px')
        popup.hidePopup()
      })
  }
}

export function createSVGforFeature (outerdiv) {
  var viewBoxwidth = document.getElementById('structure-viewer').offsetWidth - store.state.margin.right - store.state.margin.left
  width = document.getElementById('structure-viewer').offsetWidth - store.state.margin.right
  var height = 40 - store.state.margin.top - store.state.margin.bottom + 35 
  var nusvg = outerdiv.append('svg').attr('width', width)
    .attr('height', height)
    .attr('viewBox', '0 0 ' + viewBoxwidth + ' ' + height)
    .attr('preserveAspectRatio', 'none')
  return nusvg
}

