<template>
    <div id="header">
        <div id="logo" class="item fix level3"><a href="#About" @click="showScrim"><img v-bind:src="aquariasymbol" /></a></div>
        <Title />
        <Menu />
    </div>
</template>
<script>
import Title from './Title'
import Menu from './Menu'
import store from '../../store/index'
export default {
  name: 'Header',
  components: {
    Title,
    Menu
  },
  data () {
    return {
      aquariasymbol: require('../../assets/images/aquaria-symbol-01.svg')
    }
  },
  methods: {
    showScrim: function () {
      if (window.location.hash === '') {
        store.commit('setHash', '#(0,0,0)')
      }
      if (window.location.hash.includes('(')) {
        store.commit('setHash', window.location.hash)
      }
      document.querySelector('#scrim').className = 'show level3'
      document.querySelector('#xr-button').click()
    }
  }
}
</script>
<style>
#header {
  display: flex;
  flex-flow: row nowrap;
  /* Then we define how to distribute the remaining space */
  justify-content: space-between;
  margin-top: 0.5rem;
  width: 100%;
  height: 4em;
}
.item {
    flex-grow: 0;
    flex-basis: calc(2rem + 3 * ((100vw - 320px) / 680));
    display: flex;
}
.item a {
    display: flex;
}
.item  img {
    height: calc(1.75rem + 2vh);
}
#logo {
    left: 1vw;
    top: 0.75vw;
}
strong {
    font-weight: 600;
}
</style>
