import store from '../../../store/index'
var Highcharts = require('./highstocks.js')
const oneAaCodes = ['A', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'V', 'W', 'Y']
  
export function hidePopup () {
    store.commit('setPopupCoordinates', 0)
}
  
export function showPopup (f, eid) {
    var urlhtml = ''
    if (f.urls.length > 0) {
        urlhtml = '<p>'
        for (var i = 0; i < f.urls.length; i++) {
            urlhtml += "<a href='" + f.urls[i].href + "' target='_blank'>" +
                    f.urls[i].text + '</a><br>'
        }
        urlhtml += '</p>'
    }

    let oldAa = ''
    if (f.name.includes('&#8201;&#8594;&#8201;')) {
        oldAa = f.name.replace(/^.*\(/, '')
        oldAa = oldAa.replace(/\&.*$/, '')
    }

    let btnsDiv = "<div id='buttons_eachAa'> <b>See also:</b><p class='pAaColor'> " + oldAa + ' &rarr;</p>'

    oneAaCodes.forEach(function (anAa, _i) {
        if (anAa != oldAa) {
            // balloon = balloon + "<div id='divVI_varInfo_" + anAa + "'></div>"
      const isUniCosOrFun = isUniCosOrFunPresent(anAa, f.desc, f.start)
            btnsDiv = btnsDiv + "<button id='btnVI_" + anAa + "'"
            if (isUniCosOrFun == true) {
                btnsDiv = btnsDiv + "class='btnAaBold_b'> <b>" + anAa + '</b> '
            } else {
                btnsDiv = btnsDiv + " class='btnAaBold'> " + anAa
            }
            btnsDiv = btnsDiv + '</button>'
        }
    })
    btnsDiv = btnsDiv + '</div>'

    let balloonText = f.desc + "<br>" + urlhtml;

    let popupTitle = '<b>' + f.label + '</b>'
    if (f.name.includes('span_missenseHeading')){
        store.commit('setIsDefaultFeature', false)
        if (f.start != f.end){
        store.commit('setVariant', f.start + '-' + f.end)
        store.commit('setPopupTitle', f.label)
        getIdxInVarRes(f.start + '-' + f.end, f.label)
        }
        else {
        store.commit('setVariant', f.start)
        store.commit('setPopupTitle', f.label)

        getIdxInVarRes(f.start, f.label)
        }
    }
    else {
        store.commit('setVariant', -1)
        store.commit('setIsDefaultFeature', true)
        if (f.start === f.end){
            popupTitle = popupTitle + '( Residue ' + f.start + ') '
        } else {
            popupTitle = popupTitle + '( Residues ' + f.start + '-' + f.end + ') '
        }
        store.commit('setPopupTitle', popupTitle)
        store.commit('setPopupText', balloonText)
        store.commit('setVariantIdx', -1)
        store.commit('setPopupCoordinates', document.querySelector('#' + eid.id).getBoundingClientRect())
        store.commit('setPopupType', 'coveragemap')
    }

    if (f.hasOwnProperty('hc_go') && f.hasOwnProperty('hc_ec') && f.hasOwnProperty('hc_species')) {
        console.log('CATH plot data')
        console.log(f)
        handleCathPopups(f).then(function () {
            updateTheStyleOfHc()
        })
    } else {
        console.log('CATH plot no data')
        console.log(f)
    }
}

function getIdxInVarRes(resNum, popupText){
var varRes = store.getters.getVariantResidues
//console.log("GAMMA " + resNum + " " + popupText)
//console.log(varRes)


if (varRes.hasOwnProperty(resNum) && varRes[resNum].hasOwnProperty('theOrigFeatStr')){
  for (let i=0; i < varRes[resNum].theOrigFeatStr.length; i++){
    if (popupText.includes(varRes[resNum].theOrigFeatStr[i])){
      store.commit('setVariantIdx', i)
      // console.log("Set variant idx " + i)
      break
    }
  }
}
}

function isUniCosOrFunPresent (anAa, desc, resNum) {
const tag_open = '<toReplace_varInfo_' + anAa + '>'
const tag_close = '</toReplace_varInfo_' + anAa + '>'

const	firstOpenPos = desc.indexOf(tag_open)
const lastClosePos = desc.lastIndexOf(tag_close)

const description = desc.substring(firstOpenPos, lastClosePos)
var variantStructs = store.state.variantStructs.variantStructs
if (description.match('UniProt') || description.match('FunVar') || description.match('COSMIC') || (variantStructs.hasOwnProperty(resNum) && variantStructs[resNum].hasOwnProperty(anAa) && variantStructs[resNum][anAa].hasOwnProperty('pdbs') && variantStructs[resNum][anAa]['pdbs'].length > 0)) {
  return true
}
return false
}

